<template>
  <el-card style="" class="card">
    <h1 class="card">用户注册</h1>
    <el-divider />
    <el-form :label-position="top" :model="form" :rules="rules" ref="form">
      <el-form-item label="用户名:" prop="username">
        <el-input v-model="form.username" class="box-card"></el-input>
      </el-form-item>
      <el-form-item label="密码:" prop="password">
        <el-input type="password" v-model="form.password"></el-input>
      </el-form-item>
      <el-form-item label="性别:">
      <el-radio-group v-model="form.sex">
        <el-radio label="男" />
        <el-radio label="女" />
      </el-radio-group>
    </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitForm('form') && submitFormToServer()" style="width: 100%">注册</el-button>
      </el-form-item>
      <el-form-item>

    </el-form-item>
    </el-form>
  </el-card>
</template>  
  
<script>
import axios from 'axios'; // 引入 axios 库  

import { ElMessageBox } from 'element-plus'



export default {
  data() {
    return {
      form: {
        username: '',
        password: '',
        sex:'',

      },
      rules: {
        username: [
          { required: true, message: '请输入用户名', trigger: 'blur' },
          { min: 2, message: '最少两个字符', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' },
          { min: 2, max: 20, message: '长度在 2 到 20 个字符', trigger: 'blur' }
        ]
      }
    };
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.submitFormToServer(); // 调用提交到服务器的函数  

        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    submitFormToServer() {
      axios.post('http://127.0.0.1:5000/api/register', this.form)
        .then(response => {
          // 处理响应数据  
          ElMessageBox.alert("注册成功，去<a href='/login'>登录</a>", "提示",
            {
              dangerouslyUseHTMLString: true,
              confirmButtonText: '好的',
            }
          )
          console.log(response.data);



        })
        .catch(error => {
          // 处理错误  
          ElMessageBox.alert(error.response.data.message, "提示")
          console.debug(error.response.data.message);


        });
    }
  }
};  
</script>
<style>
.card {
  justify-content: center;
  align-items: center;
  text-align: center;
}
</style>